.academy__header {
    display: flex;
    flex-direction: row;
}

.academy__header-content {
    flex: 1;
    display: flex;
    justify-content: center;
    align-items: flex-start;
    flex-direction: column;

    margin-right: 5rem;
}

.academy__header-content h1 {
    font-family: var(--font-family);
    font-weight: 800;
    font-size: 62px;
    line-height: 75px;
    letter-spacing: -0.04em;
}

.academy__header-content p {
    font-family: var(--font-family);
    font-weight: 400;
    font-size: 20px;
    line-height: 28px;
    color: var(--color-text);

    margin-top: 1.5rem;
}

.button-container {
    display: flex;
    justify-content: start;
    align-items: start;
    padding: 20px 0px;
}
  
.sign-in-button,
.get-app-button {
    padding: 13.5px 35px;
    font-size: 16px;
    color: #FFFFFF;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    transition: background-color 0.3s ease;
    background: var(--color-footer);

    background:-moz-radial-gradient(circle at 30% -100%, #0745b8 25%, rgb(80, 53, 235) 85%, rgb(34, 99, 238) 100%);

    background:-webkit-radial-gradient(circle at 30% -100%, #0745b8 25%, rgb(80, 53, 235) 85%, rgb(34, 99, 238) 100%);

    background:-o-radial-gradient(circle at 30% -100%, #0745b8 25%, rgb(80, 53, 235) 85%, rgb(34, 99, 238) 100%);

    background:-ms-radial-gradient(circle at 30% -100%, #0745b8 25%, rgb(80, 53, 235) 85%, rgb(34, 99, 238) 100%);

    background:radial-gradient(circle at 30% -100%, #0745b8 25%, rgb(80, 53, 235) 85%, rgb(34, 99, 238) 100%);

    filter: drop-shadow(0px 4px 4px rgba(0, 0, 0, 0.25));
}
  
.get-app-button {
    background-color: #4caf50;
}

.academy__header-content__people {
    width: 100%;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    flex-direction: row;

    margin-top: 2rem;
}

.academy__header-content__people img {
    width: 181.79px;
    height: 38px;
}

.academy__header-content__people p {
    margin: 0 0 0 1rem;
    font-family: var(--font-family);
    font-weight: 500;
    font-size: 12px;
    line-height: 38px;
    color: #fff;
    text-align: center;
}

.academy__header-image {
    flex: 1;
    display: flex;
    justify-content: center;
    align-items: center;
}

.academy__header-image img {
    width: 100%;
    height: 100%;
}

@media screen and (max-width: 1050px) {
    .academy__header {
        flex-direction: column;
    }

    .academy__header-content {
        margin: 0 0 3rem;
    }    
}

@media screen and (max-width: 650px) {
    .academy__header-content h1 {
        font-size: 48px;
        line-height: 60px;
    }

    .button-container {
        flex-direction: column;
        align-items: stretch;
    }
      
    .sign-in-button,
    .get-app-button {
        width: 100%;
        margin-bottom: 10px;
    }

    .academy__header-content p {
        font-size: 16px;
        line-height: 24px;
    }
    
    .academy__header-content__people {
        flex-direction: column;
    }

    .academy__header-content__people p {
        margin: 0;
    }

    .academy__header-content__input input,
    .academy__header-content__input button {
        font-size: 16px;
        line-height: 24px;
    }
}

@media screen and (max-width: 490px) {
    .academy__header-content h1 {
        font-size: 36px;
        line-height: 48px;
    }

    .academy__header-content p {
        font-size: 14px;
        line-height: 24px;
    }

    .academy__header-content__input input,
    .academy__header-content__input button {
        font-size: 12px;
        line-height: 16px;
    }
}