.academy__brand {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
}

.academy__brand div {
    flex: 1;
    max-width: 150px;
    min-width: 120px;
    margin: 1rem;

    display: flex;
    justify-content: center;
    align-items: center;
}

.academy__brand p {
    margin: 0px 1.5rem 0 1.5rem;
    font-family: var(--font-family);
    font-weight: 500;
    font-size: 14px;
    line-height: 38px;
    color: #fff;
    text-align: center;
}